import React, {useEffect, useState} from 'react';
import { makeStyles, withStyles } from '@material-ui/core/styles';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableContainer from '@material-ui/core/TableContainer';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import Paper from '@material-ui/core/Paper';

const useStyles = makeStyles({
  table: {
    minWidth: 650,
  },
});
const StyledTableCell = withStyles((theme) => ({
    head: {
      backgroundColor: theme.palette.common.black,
      color: theme.palette.common.white,
    },
    body: {
      fontSize: 14,
    },
  }))(TableCell);
  
  const StyledTableRow = withStyles((theme) => ({
    root: {
      '&:nth-of-type(odd)': {
        backgroundColor: theme.palette.background.default,
      },
    },
  }))(TableRow);



export default function TableView() {
    const [rows, setRows] = useState([]);
  
  useEffect(() => {
    async function fetchStateData() {
        let response = await fetch(`https://api.covid19india.org/data.json`);
        let data = await response.json();
        let transformedData = data.statewise.map(({ state, active, confirmed, recovered, deaths }) => {
            return { state, active, confirmed, recovered, deaths }
        });
        console.log(transformedData);
        transformedData.shift();
        setRows(transformedData);
    };
    fetchStateData();
  }, []);  
  const classes = useStyles();

  return (
    <TableContainer component={Paper}>
      <Table className={classes.table} size="small" aria-label="simple table">
        <TableHead>
          <TableRow>
            <StyledTableCell>States</StyledTableCell>
            <StyledTableCell>Confirmed</StyledTableCell>
            <StyledTableCell>Active</StyledTableCell>
            <StyledTableCell>Recovered</StyledTableCell>
            <StyledTableCell>Deaths</StyledTableCell>
          </TableRow>
        </TableHead>
        <TableBody>
          {rows.map((row) => (
            <StyledTableRow key={row.state}>
              <StyledTableCell component="th" scope="row">
                {row.state}
              </StyledTableCell>
              <StyledTableCell>{row.confirmed}</StyledTableCell>
              <StyledTableCell >{row.active}</StyledTableCell>
              <TableCell >{row.recovered}</TableCell>
              <StyledTableCell >{row.deaths}</StyledTableCell>
            </StyledTableRow>
          ))}
        </TableBody>
      </Table>
    </TableContainer>
  );
}
